export default [
  { id: 1, rate: {sum: 0 ,voters: []},borrowedBy: 'Micha', name: 'The Hunger Games', author: 'Suzanne Collins', cover: 'https://images.gr-assets.com/books/1447303603l/2767052.jpg', description: "The nation of Panem, formed from a post-apocalyptic North America, is a country that consists of a wealthy Capitol region surrounded by 12 poorer districts. Early in its history, a rebellion led by a 13th district against the Capitol resulted in its destruction and the creation of an annual televised event known as the Hunger Games. In punishment, and as a reminder of the power and grace of the Capitol, each district must yield one boy and one girl between the ages of 12 and 18 through a lottery system to participate in the games. The 'tributes' are chosen during the annual Reaping and are forced to fight to the death, leaving only one survivor to claim victory." },
  { id: 2, rate: {sum: 0 ,voters: []},borrowedBy: '', name: 'Harry Potter and the Order of the Phoenix', author: ' J.K. Rowling', cover: 'https://images.gr-assets.com/books/1507396732l/2.jpg', description: "Harry Potter and the Order of the Phoenix is the fifth novel in the Harry Potter series, written by J. K. Rowling. It follows Harry Potter's struggles through his fifth year at Hogwarts School of Witchcraft and Wizardry, including the surreptitious return of the antagonist Lord Voldemort, O.W.L. exams, and an obstructive Ministry of Magic. The novel was published on 21 June 2003 by Bloomsbury in the United Kingdom, Scholastic in the United States, and Raincoast in Canada. Five million copies were sold in the first 24 hours of publication. It is the longest book of the series. Harry Potter and the Order of the Phoenix has won several awards, including being named an American Library Association Best Book for Young Adults in 2003. The book has also been made into a film, which was released in 2007, and into a video game by Electronic Arts." },
  { id: 3, rate: {sum: 0 ,voters: []},borrowedBy: '', name: 'To Kill a Mockingbird', author: 'Harper Lee', cover: 'https://images.gr-assets.com/books/1361975680l/2657.jpg', description: "The unforgettable novel of a childhood in a sleepy Southern town and the crisis of conscience that rocked it, To Kill A Mockingbird became both an instant bestseller and a critical success when it was first published in 1960. It went on to win the Pulitzer Prize in 1961 and was later made into an Academy Award-winning film, also a classic." },
  { id: 4, rate: {sum: 0 ,voters: []},borrowedBy: 'Micha', name: 'Pride and Prejudice', author: 'Jane Austen', cover: 'https://images.gr-assets.com/books/1320399351l/1885.jpg', description: "It is a truth universally acknowledged, that a single man in possession of a good fortune must be in want of a wife. So begins Pride and Prejudice, Jane Austens witty comedy of mannersone of the most popular novels of all timethat features splendidly civilized sparring between the proud Mr. Darcy and the prejudiced Elizabeth Bennet as they play out their spirited courtship in a series of eighteenth-century drawing-room intrigues. Renowned literary critic and historian George Saintsbury in 1894 declared it the most perfect, the most characteristic, the most eminently quintessential of its authors works, and Eudora Welty in the twentieth century described it as irresistible and as nearly flawless as any fiction could be. --back cover" },
  { id: 5, rate: {sum: 0 ,voters: []},borrowedBy: '', name: 'Twilight', author: 'Stephenie Meyer', cover: 'https://images.gr-assets.com/books/1361039443l/41865.jpg', description: "In the first book of the Twilight Saga, internationally bestselling author Stephenie Meyer introduces Bella Swan and Edward Cullen, a pair of star-crossed lovers whose forbidden relationship ripens against the backdrop of small-town suspicion and a mysterious coven of vampires. This is a love story with bite." },
  { id: 6, rate: {sum: 0 ,voters: []},borrowedBy: 'Micha', name: 'The Book Thief', author: 'Markus Zusak', cover: 'https://images.gr-assets.com/books/1390053681l/19063.jpg', description: "Set during World War II in Germany, Markus Zusaks groundbreaking new novel is the story of Liesel Meminger, a foster girl living outside of Munich. Liesel scratches out a meager existence for herself by stealing when she encounters something she cant resist  books. With the help of her accordion-playing foster father, she learns to read and shares her stolen books with her neighbors during bombing raids as well as with the Jewish man hidden in her basement before he is marched to Dachau. This is an unforgettable story about the ability of books to feed the soul." },
  { id: 7, rate: {sum: 0 ,voters: []},borrowedBy: '', name: 'The Chronicles of Narnia', author: 'C.S. Lewis', cover: 'https://images.gr-assets.com/books/1449868701l/11127.jpg', description: "Journeys to the end of the world, fantastic creatures, and epic battles between good and evilwhat more could any reader ask for in one book? The book that has it all is The Lion, the Witch and the Wardrobe, written in 1949 by Clive Staples Lewis. But Lewis did not stop there. Six more books followed, and together they became known as The Chronicles of Narnia." },
  { id: 8, rate: {sum: 0 ,voters: []},borrowedBy: '', name: 'Animal Farm', author: 'George Orwell', cover: 'https://images.gr-assets.com/books/1424037542l/7613.jpg', description: "As ferociously fresh as it was more than a half century ago, this remarkable allegory of a downtrodden society of overworked, mistreated animals, and their quest to create a paradise of progress, justice, and equality is one of the most scathing satires ever published. As we witness the rise and bloody fall of the revolutionary animals, we begin to recognize the seeds of totalitarianism in the most idealistic organization; and in our most charismatic leaders, the souls of our cruelest oppressors." },
  { id: 9, rate: {sum: 0 ,voters: []},borrowedBy: '', name: 'Gone with the Wind', author: 'Margaret Mitchell', cover: 'https://images.gr-assets.com/books/1328025229l/18405.jpg', description: "Gone with the Wind is a novel written by Margaret Mitchell, first published in 1936. The story is set in Clayton County, Georgia, and Atlanta during the American Civil War and Reconstruction era. It depicts the struggles of young Scarlett O'Hara, the spoiled daughter of a well-to-do plantation owner, who must use every means at her disposal to claw her way out of the poverty she finds herself in after Sherman's March to the Sea. A historical novel, the story is a Bildungsroman or coming-of-age story, with the title taken from a poem written by Ernest Dowson." },
  { id: 10, rate: {sum: 0 ,voters: []},borrowedBy: '', name: 'The Hobbit and The Lord of the Rings', author: 'J.R.R. Tolkien', cover: 'https://images.gr-assets.com/books/1346072396l/30.jpg', description: "This four volume, deluxe paperback boxed set contains J.R.R. Tolkien's epic masterworks The Hobbit and the three volumes of The Lord Of The Rings (The Fellowship Of The Ring, The Two Towers, and The Return Of The King) in their definitive text settings complete with maps and cover illustrations from the motion pictures. In The Hobbit, Bilbo Baggins is whisked away from his comfortable, unambitious life in Hobbiton by the wizard Gandalf and a company of dwarves. He finds himself caught up in a plot to raid the treasure hoard of Smaug the Magnificent, The Lord Of The Rings tells of the great and dangerous quest undertaken by Frodo Baggins and the Fellowship of the Ring: Gandalf the wizard; the hobbits Merry, Pippin, and Sam; Gimli the dwarf; Legolas the elf; Boromir of Gondor; and a tall, mysterious stranger called Strider. J.R.R. Tolkien's three volume masterpiece is at once a classic myth and a modern fairy tale - a story of high and heroic adventure set in the unforgettable landscape of Middle-Earth." },
  { id: 11, rate: {sum: 0 ,voters: []},borrowedBy: '', name: 'The Fault in Our Stars', author: 'John Green', cover: 'https://images.gr-assets.com/books/1360206420l/11870085.jpg', description: "Despite the tumor-shrinking medical miracle that has bought her a few years, Hazel has never been anything but terminal, her final chapter inscribed upon diagnosis. But when a gorgeous plot twist named Augustus Waters suddenly appears at Cancer Kid Support Group, Hazel's story is about to be completely rewritten." },
  { id: 12, rate: {sum: 0 ,voters: []},borrowedBy: '', name: 'The Hitchhiker\'s Guide to the Galaxy', author: 'Douglas Adams', cover: 'https://images.gr-assets.com/books/1513003890l/11.jpg', description: "Seconds before the Earth is demolished to make way for a galactic freeway, Arthur Dent is plucked off the planet by his friend Ford Prefect, a researcher for the revised edition of The Hitchhiker's Guide to the Galaxy who, for the last fifteen years, has been posing as an out-of-work actor. Together this dynamic pair begin a journey through space aided by quotes from The Hitchhiker's Guide (\"A towel is about the most massively useful thing an interstellar hitchhiker can have\") and a galaxy-full of fellow travelers: Zaphod Beeblebroxthe two-headed, three-armed ex-hippie and totally out-to-lunch president of the galaxy; Trillian, Zaphod's girlfriend (formally Tricia McMillan), whom Arthur tried to pick up at a cocktail party once upon a time zone; Marvin, a paranoid, brilliant, and chronically depressed robot; Veet Voojagig, a former graduate student who is obsessed with the disappearance of all the ballpoint pens he bought over the years" },
  { id: 13, rate: {sum: 0 ,voters: []},borrowedBy: '', name: 'The Giving Tree', author: 'Shel Silverstein', cover: 'https://images.gr-assets.com/books/1174210942l/370493.jpg', description: "Every day the boy would come to the tree to eat her apples, swing from her branches, or slide down her trunk...and the tree was happy. But as the boy grew older he began to want more from the tree, and the tree gave and gave and gave." },
  { id: 14, rate: {sum: 0 ,voters: []},borrowedBy: '', name: 'Wuthering Heights', author: 'Emily Bront', cover: 'https://images.gr-assets.com/books/1388212715l/6185.jpg', description: "Wuthering Heights is a wild, passionate story of the intense and almost demonic love between Catherine Earnshaw and Heathcliff, a foundling adopted by Catherine's father. After Mr Earnshaw's death, Heathcliff is bullied and humiliated by Catherine's brother Hindley and wrongly believing that his love for Catherine is not reciprocated, leaves Wuthering Heights, only to return years later as a wealthy and polished man. He proceeds to exact a terrible revenge for his former miseries. The action of the story is chaotic and unremittingly violent, but the accomplished handling of a complex structure, the evocative descriptions of the lonely moorland setting and the poetic grandeur of vision combine to make this unique novel a masterpiece of English literature." },
  { id: 15, rate: {sum: 0 ,voters: []},borrowedBy: '', name: 'The Da Vinci Code', author: 'Dan Brown', cover: 'https://images.gr-assets.com/books/1303252999l/968.jpg', description: "While in Paris on business, Harvard symbologist Robert Langdon receives an urgent late-night phone call: the elderly curator of the Louvre has been murdered inside the museum. Near the body, police have found a baffling cipher. While working to solve the enigmatic riddle, Langdon is stunned to discover it leads to a trail of clues hidden in the works of Da Vinci -- clues visible for all to see -- yet ingeniously disguised by the painter." },
  { id: 16, rate: {sum: 0 ,voters: []},borrowedBy: '', name: 'Memoirs of a Geisha', author: 'Arthur Golden', cover: 'https://images.gr-assets.com/books/1388367666l/930.jpg', description: "In Memoirs of a Geisha, we enter a world where appearances are paramount; where a girl's virginity is auctioned to the highest bidder; where women are trained to beguile the most powerful men; and where love is scorned as illusion. It is a unique and triumphant work of fiction - at once romantic, erotic, suspenseful - and completely unforgettable." },
  { id: 17, rate: {sum: 0 ,voters: []},borrowedBy: '', name: 'Alice\'s Adventures in Wonderland', author: 'Lewis Carroll', cover: 'https://images.gr-assets.com/books/1327872220l/24213.jpg', description: "The Ugly Duchess, the Mad Hatter, the weeping Mock Turtle, the diabolical Queen of Hearts, the Cheshire Cat--each more eccentric than the last--could only have come from that master of sublime nonsense, Lewis Carroll." },
  { id: 18, rate: {sum: 0 ,voters: []},borrowedBy: '', name: 'The Picture of Dorian Gray', author: 'Oscar Wilde', cover: 'https://images.gr-assets.com/books/1424596966l/5297.jpg', description: "Written in his distinctively dazzling manner, Oscar Wildes story of a fashionable young man who sells his soul for eternal youth and beauty is the authors most popular work. The tale of Dorian Grays moral disintegration caused a scandal when it rst appeared in 1890, but though Wilde was attacked for the novels corrupting inuence, he responded that there is, in fact, a terrible moral in Dorian Gray. Just a few years later, the book and the aesthetic/moral dilemma it presented became issues in the trials occasioned by Wildes homosexual liaisons, which resulted in his imprisonment. Of Dorian Grays relationship to autobiography, Wilde noted in a letter, Basil Hallward is what I think I am: Lord Henry what the world thinks me: Dorian what I would like to bein other ages, perhaps." },
  { id: 19, rate: {sum: 0 ,voters: []},borrowedBy: '', name: 'Les Misrables', author: 'Victor Hugo', cover: 'https://images.gr-assets.com/books/1411852091l/24280.jpg', description: "Introducing one of the most famous characters in literature, Jean Valjeanthe noble peasant imprisoned for stealing a loaf of breadLes Misrables ranks among the greatest novels of all time. In it, Victor Hugo takes readers deep into the Parisian underworld, immerses them in a battle between good and evil, and carries them to the barricades during the uprising of 1832 with a breathtaking realism that is unsurpassed in modern prose. Within his dramatic story are themes that capture the intellect and the emotions: crime and punishment, the relentless persecution of Valjean by Inspector Javert, the desperation of the prostitute Fantine, the amorality of the rogue Thnardier, and the universal desire to escape the prisons of our own minds. Les Misrables gave Victor Hugo a canvas upon which he portrayed his criticism of the French political and judicial systems, but the portrait that resulted is larger than life, epic in scopean extravagant spectacle that dazzles the senses even as it touches the heart" },
  { id: 20, rate: {sum: 0 ,voters: []},borrowedBy: '', name: 'Divergent ', author: 'Veronica Roth', cover: 'https://images.gr-assets.com/books/1328559506l/13335037.jpg', description: "In Beatrice Prior's dystopian Chicago world, society is divided into five factions, each dedicated to the cultivation of a particular virtueCandor (the honest), Abnegation (the selfless), Dauntless (the brave), Amity (the peaceful), and Erudite (the intelligent). On an appointed day of every year, all sixteen-year-olds must select the faction to which they will devote the rest of their lives. For Beatrice, the decision is between staying with her family and being who she really isshe can't have both. So she makes a choice that surprises everyone, including herself." },
  { id: 21, rate: {sum: 0 ,voters: []},borrowedBy: '', name: 'Romeo and Juliet', author: 'William Shakespeare', cover: 'https://images.gr-assets.com/books/1327872146l/18135.jpg', description: "In Romeo and Juliet, Shakespeare creates a world of violence and generational conflict in which two young people fall in love and die because of that love. The story is rather extraordinary in that the normal problems faced by young lovers are here so very large. It is not simply that the families of Romeo and Juliet disapprove of the lover's affection for each other; rather, the Montagues and the Capulets are on opposite sides in a blood feud and are trying to kill each other on the streets of Verona. Every time a member of one of the two families dies in the fight, his relatives demand the blood of his killer. Because of the feud, if Romeo is discovered with Juliet by her family, he will be killed. Once Romeo is banished, the only way that Juliet can avoid being married to someone else is to take a potion that apparently kills her, so that she is burried with the bodies of her slain relatives. In this violent, death-filled world, the movement of the story from love at first sight to the union of the lovers in death seems almost inevitable." },
  { id: 22, rate: {sum: 0 ,voters: []},borrowedBy: '', name: 'Jane Eyre', author: 'Charlotte Bront', cover: 'https://images.gr-assets.com/books/1327867269l/10210.jpg', description: "Fiery love, shocking twists of fate, and tragic mysteries put a lonely governess in jeopardy in JANE EYRE. Orphaned as a child, Jane has felt an outcast her whole young life. Her courage is tested once again when she arrives at Thornfield Hall, where she has been hired by the brooding, proud Edward Rochester to care for his ward Adle. Jane finds herself drawn to his troubled yet kind spirit. She falls in love. Hard. But there is a terrifying secret inside the gloomy, forbidding Thornfield Hall. Is Rochester hiding from Jane? Will Jane be left heartbroken and exiled once again?" },
  { id: 23, rate: {sum: 0 ,voters: []},borrowedBy: '', name: 'Lord of the Flies', author: 'William Golding', cover: 'https://images.gr-assets.com/books/1327869409l/7624.jpg', description: "The book focuses on a group of British boys stranded on an uninhabited island and their disastrous attempt to govern themselves." },
  { id: 24, rate: {sum: 0 ,voters: []},borrowedBy: '', name: 'The Alchemist', author: 'Paulo Coelho', cover: 'https://images.gr-assets.com/books/1483412266l/865.jpg', description: "The Alchemist by Paulo Coelho continues to change the lives of its readers forever. With more than two million copies sold around the world, The Alchemist has established itself as a modern classic, universally admired." },
  { id: 25, rate: {sum: 0 ,voters: []},borrowedBy: '', name: 'Crime and Punishment', author: 'Fyodor Dostoyevsky', cover: 'https://images.gr-assets.com/books/1382846449l/7144.jpg', description: "Raskolnikov, a destitute and desperate former student, wanders through the slums of St Petersburg and commits a random murder without remorse or regret. He imagines himself to be a great man, a Napoleon: acting for a higher purpose beyond conventional moral law. But as he embarks on a dangerous game of cat and mouse with a suspicious police investigator, Raskolnikov is pursued by the growing voice of his conscience and finds the noose of his own guilt tightening around his neck. Only Sonya, a downtrodden prostitute, can offer the chance of redemption." },
  { id: 26, rate: {sum: 0 ,voters: []},borrowedBy: '', name: 'Ender\'s Game', author: 'Orson Scott Card', cover: 'https://images.gr-assets.com/books/1408303130l/375802.jpg', description: "Ender's Game is a 1985 military science fiction novel by American author Orson Scott Card. Set in Earth's future, the novel presents an imperiled mankind after two conflicts with the \"buggers\", an insectoid alien species. In preparation for an anticipated third invasion, children, including the novel's protagonist, Ender Wiggin, are trained from a very young age through increasingly difficult games including some in zero gravity, where Ender's tactical genius is revealed." },
  { id: 27, rate: {sum: 0 ,voters: []},borrowedBy: '', name: 'City of Bones', author: 'Cassandra Clare', cover: 'https://images.gr-assets.com/books/1432730315l/256683.jpg', description: "When fifteen-year-old Clary Fray heads out to the Pandemonium Club in New York City, she hardly expects to witness a murder much less a murder committed by three teenagers covered with strange tattoos and brandishing bizarre weapons. Then the body disappears into thin air. It's hard to call the police when the murderers are invisible to everyone else and when there is nothingnot even a smear of bloodto show that a boy has died. Or was he a boy?" },
  { id: 28, rate: {sum: 0 ,voters: []},borrowedBy: '', name: 'The Perks of Being a Wallflower', author: 'Stephen Chbosky', cover: 'https://images.gr-assets.com/books/1167352178l/22628.jpg', description: "The critically acclaimed debut novel from Stephen Chbosky, Perks follows observant wallflower Charlie as he charts a course through the strange world between adolescence and adulthood. First dates, family drama, and new friends. Sex, drugs, and The Rocky Horror Picture Show. Devastating loss, young love, and life on the fringes. Caught between trying to live his life and trying to run from it, Charlie must learn to navigate those wild and poignant roller-coaster days known as growing up." },
  { id: 29, rate: {sum: 0 ,voters: []},borrowedBy: '', name: 'The Help', author: 'Kathryn Stockett', cover: 'https://images.gr-assets.com/books/1346100365l/4667024.jpg', description: "In pitch-perfect voices, Kathryn Stockett creates three extraordinary women whose determination to start a movement of their own forever changes a town, and the way women  mothers, daughters, caregivers, friends  view one another. A deeply moving novel filled with poignancy, humor, and hope, The Help is a timeless and universal story about the lines we abide by, and the ones we don't." },
  { id: 30, rate: {sum: 0 ,voters: []},borrowedBy: '', name: 'The Time Traveler\'s Wife', author: 'Audrey Niffenegger', cover: 'https://images.gr-assets.com/books/1380660571l/18619684.jpg', description: "A funny, often poignant tale of boy meets girl with a twist: what if one of them couldn't stop slipping in and out of time? Highly original and imaginative, this debut novel raises questions about life, love, and the effects of time on relationships." }
];


